.Overlay {
    display: flex;
    flex-direction: column;
    z-index: 1;
    width: 500px;
    height: 500px;
    opacity: 0;
    margin: 30px;
    background-color: white;
    text-align: center;
    -webkit-transition: .6s ease;
    transition: .6s ease;
    border-radius: 250px;
    box-shadow: 0 2px 2px 0 rgba(171, 171, 171, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Overlay:hover:not(.Project-title) {
    opacity: 0.90;
}

.Project-title {
